import './App.css';

// $(document).ready(function() {
//
// });

function home() {
    // eslint-disable-next-line no-undef
    let someInternalVariable = L;

    var map = someInternalVariable.map('map', {
        crs: someInternalVariable.CRS.Simple,
        minZoom: -5
    });

    var bounds = [[0, 0], [1000, 1000]];
    var stavs1 = someInternalVariable.imageOverlay('zm1.svg', bounds).addTo(map);
    var stavs2 = someInternalVariable.imageOverlay('zm2.svg', bounds).addTo(map);
    var stavs3 = someInternalVariable.imageOverlay('zm3.svg', bounds).addTo(map);
    var stavs4 = someInternalVariable.imageOverlay('zm4.svg', bounds).addTo(map);
    var stavs5 = someInternalVariable.imageOverlay('zm5.svg', bounds).addTo(map);

    var baseMaps = {
        "1": stavs1,
        "2": stavs2,
        "3": stavs3,
        "4": stavs4,
        "5": stavs5
    };

    someInternalVariable.control.layers(baseMaps).addTo(map);

    map.fitBounds(bounds);


    return (
        <div id="map"/>
        // you can plug any html here...
    );
}

export default home;
